.PreFooter2{
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 2rem 0;
}
.Footer2>div{
    display: flex;
    align-items: center;
    border: 2px solid black;
}
.Footer2>div>button{
    border: none;
    background-color: white;
}
.Footer2>div>i{
    height:2.5rem;
}
.Footer2 input{
    border: none;
    outline: none;
    padding: .5vmax 1vmax;
    background-color: white;
    font-size: 1vmax;
    width: 20.4vmax;
}
.Footer4 i{
   margin: 0 .5rem;
   color: #777;
   cursor: pointer;
}
.Footer4 i:hover{
    color: #222;
}