.HotAccessories{
    display: flex;
    justify-content: center;
    background-color: whitesmoke;
}
#HotCover{
    height: 100%;
    margin-left: 4rem;
}
.HotElements{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin-left: 2rem;
}
.HotElements>div:hover{
    transform: translateY(-4px);
    box-shadow: 0px 5px 5px 5px #bbb;
}
.HotElements>div{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 1rem;
    padding: .5rem;
    cursor: pointer;
    transition: all .3s ease-in-out;
    background-color: white;
}
.HotElements img{
    width: 18rem;
}
.HotElementName{
    color: #666;
    font-weight: 500;
    margin-bottom: -.4rem;
}
.HotElementPrice{
    font-weight: 500;
    color: orangered;
}