.Footer{
    display: flex;
    justify-content: space-around;
    padding: 3rem;
    background-color: #333;
    color: white;
}
.Footer a{
    text-decoration: none;
    color: white;
    margin: 5px;
}
.support{
    display: flex;
    flex-direction: column;
}
.retailStore{
    display: flex;
    flex-direction: column;
}
.aboutUS{
    display: flex;
    flex-direction: column;
}
.contactUs{
    display: flex;
    flex-direction: column;
}
.bot{
    display: flex;
    flex-direction: column;
}
.special{
    display: flex;
    flex-wrap: wrap;
    width: 10rem;
}   
.bot button{
    background-color: #333;
    border: 2px solid white;
    color: white;
    padding: .5rem 1rem;
}