.PreFooter{
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 1rem 5rem;
    background-color: white;
    height: 7rem;
}
.PreFooter>div{
    display: flex;
    justify-content: center;
    align-items: center;
    color: #777;
    cursor: pointer;
}
.PreFooter>div:hover{
    color: #333;
}
.PreFooter>div>div{
    margin-left: 1rem;
}
.PreFooter>div>div>p{
    margin-top: -3px;
    font-weight: 500;
}
.PreFooter>div>div>h5{
    font-weight: 700;
}