.ProductReviewCard {
    background-color: white;
    margin: 0 2rem;
    transition: all .2s ease-in;
    padding-bottom: 1rem;
}

.ProductReviewCard h5 {
    color: #555;
    font-family: 'Poppins', sans-serif;
    font-weight: 500;
    margin-top: 1rem;
    margin-left: 1.5rem;
}

.ProductReviewCard p {
    margin-left: 1.5rem;
    color: #333;
}

.ProductReviewCard b {
    margin-left: 1.5rem;
    color: orangered;
    margin-bottom: -5px;
}

.ProductReviewCard:hover {
    transform: translateY(-.4vmax);
    box-shadow: 0px 6px 15px rgb(182, 175, 175);
}