.PreNavbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 3rem;
    background-color: #212121;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    padding: 0 4rem;
}
.PreNavbar a{
    text-decoration: none;
    color: rgba(255, 255, 255, 0.474);
    margin: 0 .8rem;
    font-size: 14px;
}
.PreNavbar .bar{
    color: rgba(255, 255, 255, 0.474);
}
.PreNavbarLeft,.PreNavbarRight{
    display: flex;
    justify-content: center;
    align-items: center;
}
.PreNavbar a:hover{
    color: white;
}
.PreNavbar i{
    margin-right: .3rem;
}
