.Offer{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 1.5rem;
}
.Offer img{
    margin: 0 1rem;
    cursor: pointer;
    transition: all .2s ease-in;
}
.Offer img:hover{
    transform: translateY(-.4vmax);
    box-shadow: 0px 6px 15px rgb(182, 175, 175);
}