.Navbar{
    display: flex;
    justify-content:space-around;
    align-items: center;
    height: 5.2rem;
    border-bottom: 1px solid rgb(177, 175, 175);
}
.Navbar .products{
    display: flex;
    justify-content: space-around;
    width: 55%;
}
.Navbar .products a{
    text-decoration: none;
    color: #555;
    font-family: 'Poppins', sans-serif;
    font-weight: 500;
    transition: all .2s ease-in;
}
.Navbar .products a:hover{
    color: orangered;
}
#logoImage{
    color: black;
    background-color: orangered;
    transform: translateX(-55px);
    transition: all .3s ease-in-out;
}
#logoImage:hover{
    transform: translateX(0px);
}
.logo{
    width: 56px;
    height: 56px;
    overflow: hidden;
    border-radius: 20px;
    position: relative;
    left: 4rem;
}
.searchSection input{
    border: none;
    border-bottom: 1px solid #555;
    outline:none;
}
.searchSection i{
    margin-left: 3px;
    cursor: pointer;
}
