.VideoCard {
    margin: 1rem;
    position: relative;
    cursor: pointer;
}

.videoMiddle {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    text-align: center;
    color: white;
}
.videoMiddle i{
    opacity: .55;
}
.VideoCard:hover .videoMiddle i{
    opacity: .95;
}