.Heading{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 2rem 0;
    background-color: whitesmoke;
}
.Heading>h2:first-child{
    width: 30vw;
    border: 1px solid #aaa;
    height: 1px;
    margin-right: 3rem;
}
.Heading>h2:nth-child(2){
    color: #666;
    font-weight: 400;
}
.Heading>h2:nth-child(3){
    width: 30vw;
    border: 1px solid #aaa;
    height: 1px;
    margin-left: 3rem;
}